<script>
import Vue from "vue";
import { mapState } from "vuex";
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";

const VueScrollTo = VueScrollTo;

export default {
 mounted() {
    this.$store.dispatch(SET_BREADCRUMB, [
      { title: "Features"},
      { title: "Services",route: "services" }
    ]);
  },
  data() {
    return {
      services: [],
      edit: false,
      record_id: "",
      form: {
        title: null,
        description: null,
      },
      search: "",
      headers: [
        {
          text: "ID",
          align: "start",
          sortable: false,
          value: "id",
        },
        { text: "Title", value: "title" },
        { text: "Description", value: "description" },
        { text: "Actions", value: "actions", sortable: false },
      ],
    };
  },

  created() {
    this.fetchServices();
  },
  methods: {
    getValidationState({ dirty, validated, valid = null }) {
      return dirty || validated ? valid : null;
    },
 
    resetForm() {
      this.form = {
        title: null,
        description: null,
      };

      this.$nextTick(() => {
        this.$refs.observer.reset();
      });
    },
    fetchServices() {
      this.$ApiService
        .get("/Services")
        .then((response) => {
          //console.log(response.data);
          this.services = response.data.data;
        })
        .catch((err) => Vue.$toast.error(`${err}`));
    },

    addService() {
      if (this.edit === false) {
        // add new record

        this.$ApiService
          .post("/Service", this.form)
          .then((response) => {
            if (response.data.error == 1) {
              this.resetForm();

              this.$refs.table.scrollIntoView();
              this.fetchServices();
              Vue.$toast.success(`${response.data.result}`);
              
            } else {
              console.log(response.data);
              Vue.$toast.error(`${response.data.result}`);
            }
          })
          .catch((err) => Vue.$toast.error(`${err}`));
      } else {
        //update record
        this.update();
      }
    },

    onSubmit() {
      this.addService();
    },
    addItem() {
      this.edit = false;
      this.resetForm();
    },
    editItem(item) {
      this.edit = true;
      this.record_id = item.id;
      this.form.title = item.title;
      this.form.description = item.description;
    },
    deleteItem(item) {
      this.$confirm({
        title: "Confirmation",
        message: `Are you sure you want to proceed?`,
        button: {
          no: "No",
          yes: "Yes",
        },
        callback: (confirm) => {
          if (confirm) {
            this.$ApiService
              .delete(`/Service/${item.id}`)
              .then((response) => {
                if (response.data.error == 1) {
                  this.$refs.table.scrollIntoView();
                  this.fetchServices();
                  Vue.$toast.success(`${response.data.result}`);
                } else {
                  Vue.$toast.error(`${response.data.result}`);
                }
              })
              .catch((err) => Vue.$toast.error(`${err}`));
          }
        },
      });
    },
    update() {
      this.$ApiService
        .put(`/Service/${this.record_id}`,this.form)
        .then((response) => {
          if (response.data.error == 1) {
            this.resetForm();
            this.edit = false;
            this.record_id = "";

            this.$refs.table.scrollIntoView();
            this.fetchServices();
            Vue.$toast.success(`${response.data.result}`);
            
          } else {
            Vue.$toast.error(`${response.data.result}`);
          }
        })
        .catch((err) => Vue.$toast.error(`${err}`));
    },
  }, // end of methods
    computed: {
    ...mapState({
      errors: state => state.auth.errors
      
    }),
    getUser: function () {
     
      return this.$store.getters.currentUser.data;
    }
   
  }
}; //end of class
</script>
